body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.search-section {
    width: 400px;
    height: 40px;
    vertical-align: middle;
    white-space: nowrap;
    position: relative;
}

.search-section input {
    width: 400px;
    height: 40px;
    font-size: 10pt;
    border: none;
    float: left;
    color: #282c34;
    padding: 0px 45px 0px 25px;
    border-radius: 25px;
    outline: none;
}

.search-section span {
    position: absolute;
    right: 0px;
    margin-right: 17px;
    margin-top: 4px;
    z-index: 1;
    color: #282c34;
}

.result-section {
    width: 400px;
    border: none;
    border-top: 0px;
    border-radius: 0px 0px 25px 25px;
    margin-top: -20px;
    background-color: white;
}

.result-section ul {
    color: #282c34;
    list-style-type: none;
    padding: 0px;
}

.result-section li {
    border-top: 1px #dfdfdf dotted;
    padding: 10px;
    cursor: pointer;
}
.sub-key {
    font-size: 11px;
}

.key {
    font-size: 14px;
    margin-right: 10px;
}

.value {
    font-size: 17px;
}

tr {
    cursor: pointer;
}
